import React, { useEffect, useState } from "react";
import PremiersFilms from "../../components/PremiersFilms/PremiersFilms";
import Skeleton from "../../components/Skeleton/Skeleton";
import s from "./Home.module.scss";
import Top100 from "../../components/Top100/Top100";
import Genres from "../../components/Genres/Genres";

export default function Home() {
  let [moviesPremier, setMoviesPremier] = useState([]);

  useEffect(() => {
    function getMovies() {
      let protectedUrl = "https://api.kinopoisk.dev/v1.3/movie?premiere.world=01.01.2023-31.12.2023&limit=30&rating.kp=8-9";
      fetch(protectedUrl, {
        method: "GET",
        headers: {
          "X-API-KEY": process.env.REACT_APP_KEY,
        },
      })
        .then((resp) => resp.json())
        .then((data) => {
          setMoviesPremier(data.docs);
        });
    }
    getMovies();
  }, []);

  let [topMovies, setTopMovies] = useState([]);
  useEffect(() => {
    function getMovies() {
      let protectedUrl = "https://api.kinopoisk.dev/v1.3/movie?top250=!null&limit=100";
      fetch(protectedUrl, {
        method: "GET",
        headers: {
          "X-API-KEY": process.env.REACT_APP_KEY,
        },
      })
        .then((resp) => resp.json())
        .then((data) => {
          setTopMovies(data.docs);
        });
    }
    getMovies();
  }, []);

  return (
    <div className={s.wrapper}>
      {moviesPremier.length > 0 ? <PremiersFilms title="Лучшие премьеры года" movies={moviesPremier} /> : <Skeleton />}
      {topMovies.length > 0 ? <Top100 title="Топ 100 лучших фильмов" topMovies={topMovies} /> : <Skeleton />}
      <Genres title="Жанры:" />
    </div>
  );
}
